//建立链接
import store from '../vuex/store'
var socket
let userId
// console.log(localStorage.getItem("userInfo"))

function sendSock(val) {
    console.log(val)
    userId = val
    //获取ip地址
    let host = "localhost:9000";
    // let host = window.document.location.host;
    if (typeof WebSocket === "undefined") {
        alert("您的浏览器不支持socket");
    } else {
        // 实例化socket
        // console.log(this.$route.query.userId);
        socket = new WebSocket("ws://" + host + "/websocket/" + val);
        // 监听socket连接
        socket.onopen = open;
        // 监听socket错误信息
        socket.onerror = error;
        // 监听socket消息
        socket.onmessage = getMessage;
    }
}
function open() {
    // sendPing()
    console.log("socket连接成功");
}
function error() {
    // clearInterval(setIntervalWesocketPush)
    socket.close()
    // sendSock()//重连
    alert("连接错误");
}
function getMessage(msg) {
    var userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    let str = msg.data;
    let data = JSON.parse(str);
    console.log(data)
    //心跳
    if (data.dataGroup == "0" && data.dataType == "1") {
        if (data.code !== 200) {
            // sendSock()
        }
    }

    if (data.dataGroup == "1" && data.dataType == "2") {
        //列表收消息
        store.commit("updateBaseMsg", data)
        //会话界面接受消息，接受的人等于当前登陆的号就会接收到消息，发的人接收不到
        if (data.data.to == userInfo.userId) {
            if (data.data.msgType == "text") {
                let obj = {
                    msgType: "text",
                    msg: data.data.msg,
                    roomId: data.data.roomId,
                    user: {
                        headerUrl: data.data.user.headerUrl
                    }
                }
                store.commit("addChat", obj);

            } else if (data.data.msgType == "img") {
                let obj = {
                    msgType: "img",
                    msg: data.data.fileUrl,
                    roomId: data.data.roomId,
                    user: {
                        headerUrl: data.data.user.headerUrl
                    }
                }
                store.commit("addChat", obj);
            }

        }

        //保存弹框消息
        let obj = {
            msg: data.data.msg,
            user: data.data.user
        }
        store.commit("toastMsg", obj);

    }
    //新建消息列表更新
    if (data.dataGroup == "1" && data.dataType == "3") {

        store.commit("updateBaseMsgList", data.data)
    }
    //群聊接收消息
    if (data.dataGroup == "1" && data.dataType == "4") {
        console.log(data)
        if (data.data.from != userInfo.userId) {
            if (data.data.msgType == "text") {
                let obj = {
                    msgType: "text",
                    msg: data.data.msg,
                    user: {
                        headerUrl: data.data.user.headerUrl
                    }
                }
                store.commit("addChat", obj);

            } else if (data.data.msgType == "img") {
                let obj = {
                    msgType: "img",
                    msg: data.data.fileUrl,
                    user: {
                        headerUrl: data.data.user.headerUrl
                    }
                }
                store.commit("addChat", obj);
            }
        }

        //列表收消息
        store.commit("updateBaseMsg", data)

    }
    //拉人消息
    if (data.dataGroup == "1" && data.dataType == "5") {
        console.log(data)
    }
    //新消息角标
    if (data.dataGroup == "1" && data.dataType == "11") {
        store.commit("addMsgCount", data.data)
        // console.log(store.state.msgList.baseMsg)
    }
}

function close() {
    socket.close()
    // clearInterval(setIntervalWesocketPush)
    console.log("socket已经关闭");
}

let val = {
    // from: localStorage.getItem("userInfo").userId,
    dataGroup: '0',
    dataType: '1',
    msg: 'ping'
}

function send(params) {
    console.log(params)
    //发送消息
    socket.send(JSON.stringify(params));
}



