
<template>
  <div>
    <div class="wrap" v-if="showWrap" :class="showContent ?'fadein':'fadeout'">
      <div>
        <!-- <img :src="img" /> -->
        {{text}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  }
};
</script>
<style scoped>
.wrap {
  position: relative;
  /* left: 50%;
  top: 50%; */
  background: rgba(0, 0, 0, 0.35);
  padding: 10px;
  border-radius: 5px;
  /* top: 0;
  left: 0; */
  z-index: 1000;
  text-align: center;
  /* transform: translate(-50%, -50%); */
  color: #fff;
}
.fadein {
  animation: animate_in 0.25s;
}
.fadeout {
  animation: animate_out 0.25s;
  opacity: 0;
}
@keyframes animate_in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes animate_out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
</style>


