<template>
  <div id="wx-nav">
    <nav>
      <router-link to="/wechat" tag="dl" exact>
        <dt class="iconfont icon-wechat">
          <i class="new-msg-count" v-show="newMsgCount!=0">{{newMsgCount}}</i>
        </dt>
        <dd>消息</dd>
      </router-link>
      <router-link to="/workbench" tag="dl">
        <dt class="iconfont icon-find">
          <i class="new-msg-count">{{newWorkCount}}</i>
        </dt>
        <dd>工作</dd>
      </router-link>
      <router-link to="/contact" tag="dl">
        <dt class="iconfont icon-contact">
          <!--<i class="new-msg-count">2</i>-->
        </dt>
        <dd>通讯录</dd>
      </router-link>
      <!--<router-link to="/explore" tag="dl">
        <dt class="iconfont icon-find">
          <i class="new-msg-dot"></i>
        </dt>
        <dd>发现</dd>
      </router-link>-->
      <router-link to="/self" tag="dl">
        <dt class="iconfont icon-me">
          <!--<i class="new-msg-dot"></i>-->
        </dt>
        <dd>我</dd>
      </router-link>
    </nav>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },

  computed: {
    newWorkCount() {
      return this.$store.state.newWorkCount;
    },
    newMsgCount() {
      var count = 0;
      this.$store.state.msgList.baseMsg.map(item => {
        count += item.newMsgCount;
      });
      return count;
    }
  },
  mounted() {
    // for (var i in this.$store.state.msgList.baseMsg) {
    //   if (this.$store.state.msgList.baseMsg[i].read === false && this.$store.state.msgList.baseMsg[i].quiet === false) {
    //     this.$store.commit('addNewMsg')
    //   }
    // }
    // this.newWorkCount=this.$store.state.newWorkCount;
    // this.newMsgCount = this.$store.getters.newMsgCount;
  }
};
</script>
<style>
@import "../../assets/css/wx-nav.css";
</style>



