

export default {
    /** 
 * 时间戳转化为年 月 日 时 分 秒 
 * number: 传入时间戳 
 * format：返回格式，支持自定义，但参数必须与formateArr里保持一致 
*/  formatTime(number, format) {
        //数据转化  
        function formatNumber(n) {
            n = n.toString()
            return n[1] ? n : '0' + n
        }
        var formateArr = ['Y', 'M', 'D', 'h', 'm', 's'];
        var returnArr = [];

        var date = new Date(number);
        returnArr.push(date.getFullYear());
        returnArr.push(formatNumber(date.getMonth() + 1));
        returnArr.push(formatNumber(date.getDate()));

        returnArr.push(formatNumber(date.getHours()));
        returnArr.push(formatNumber(date.getMinutes()));
        returnArr.push(formatNumber(date.getSeconds()));

        for (var i in returnArr) {
            format = format.replace(formateArr[i], returnArr[i]);
        }
        return format;
    },


    //时间戳
    timeStamp(date) {//传入时间戳，获得获取时间
        let end_date = new Date();
        let s = (end_date - (new Date(date.replace(/-/g, "\/")))) / 1000;
        let day = parseInt(s / 60 / 60 / 24);
        let hour = parseInt(s / 60 / 60);
        let minutes = parseInt(s / 60);
        let time = "";
        if (s >= 0 && s < 10) {
            // time="刚刚";
            time = false;
        } else if (60 < s) {
            // time=minutes+"分钟前";
            time = true;
        }
        return time
    },
    nowTime() {//获得当前年月日时分秒(字符串格式)
        var date = new Date;
        var nowDate = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
        return nowDate;
    },
    //获取当前时间
    time() {
        var date = new Date;
        var mon = date.getMonth() + 1;
        var day = date.getDate();
        var nowDate = date.getFullYear() + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day);
        return nowDate;
    },
    //获取当年  2019
    yearDate() {
        var date = new Date;
        var yearDate = '' + date.getFullYear();
        return yearDate;
    },
    //获取当月(-年-月)  2019-08
    month() {
        var date = new Date;
        var mon = date.getMonth() + 1
        var month = date.getFullYear() + "-" + (mon < 10 ? ('0' + mon) : mon)
        return month;
    },
    theMonth() {  //08
        var date = new Date;
        var mon = date.getMonth() + 1;
        var month = mon < 10 ? ('0' + mon) : mon;
        return month;
    },
    //获取指定日期前一天
    getBeforeDay(d) {
        d = new Date(d);
        d = +d - 1000 * 60 * 60 * 24;
        d = new Date(d);
        var year = d.getFullYear();
        var mon = d.getMonth() + 1;
        var day = d.getDate();
        var s = year + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day);
        return s;
    },
    //标准时间转换成字符串日期格式(-年-月-日)
    dateToString(date) {
        var year = date.getFullYear();
        var month = (date.getMonth() + 1).toString();
        var day = (date.getDate()).toString();
        if (month.length == 1) {
            month = "0" + month;
        }
        if (day.length == 1) {
            day = "0" + day;
        }
        var dateTime = year + "-" + month + "-" + day;
        return dateTime;
    },
    //标准时间转换成字符串日期格式(-年-月)
    monthToString(date) {
        var year = date.getFullYear();
        var month = (date.getMonth() + 1).toString();
        if (month.length == 1) {
            month = "0" + month;
        }
        var dateTime = year + "-" + month;
        return dateTime;
    },
    //标准时间转换成字符串日期格式(-年)
    yearToString(date) {
        var year = date.getFullYear();
        return year;
    },
    //数字格式化（每3位逗号隔开，保留n位小数）
    numChange(data, n) {
        var newData = data ? data.toFixed(n).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,') : "-";
        return newData;
    },
    /**
     * 转化为百分比保留一位小数
     *  */
    numtoFixed(n) {

        try {
            var bb = isNaN(n);
            if (undefined == n || null == n || '' == n || isNaN(n)) {
                return '0.0';
            }
            return (n * 100).toFixed(1);
        } catch (e) {

            return '0.0%';
        }
    },
    nullChangeLine(value) {//无值时以'-'形式出现
        let n = value;
        if (!n) {
            n = '-';
        }
        return n
    },
    hourArray() {//获取固定时刻数组
        let arr = ["00:00",
            "01:00",
            "02:00",
            "03:00",
            "04:00",
            "05:00",
            "06:00",
            "07:00",
            "08:00",
            "09:00",
            "10:00",
            "11:00",
            "12:00",
            "13:00",
            "14:00",
            "15:00",
            "16:00",
            "17:00",
            "18:00",
            "19:00",
            "20:00",
            "21:00",
            "22:00",
            "23:00"]
        return arr
    },
    daysInMonth(year, month) {//传入年和月，获取当月日期天数数组（01,02,....30,31）
        let date = new Date(year, month, 0).getDate(); //获取月份的天数
        let xDate = [];
        for (var i = 1; i <= date; i++) {
            if (i < 10) {
                i = "0" + i;
            }
            xDate.push(i);
        }
        return xDate
    },
    monthArray() {//获取月份数组
        let arr = ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"];
        return arr;
    },
    maxLoad() {//负荷最大值
        let max = 1500000;
        return max;
    },
    minLoad() {//负荷最小值
        let mim = 0;
        return mim;
    },
    maxElect() {//电量最大值
        let max = 12;
        return max;
    },
    minElect() {//电量最大值
        let min = 0;
        return min;
    },
    isNull(val) {
        return val == null || val == '' || val == "null" || val == undefined
    },
    zeroVal(data) {
        if (data) {
            return data;
        } else {
            if (data == 0) {
                return data;
            } else {
                return "-";
            }
        }

    }
}



