<template>
  <div>
    <van-popup
      v-model="isShow"
      position="top"
      get-container="body"
      :overlay="false"
      closeable
      :style="{ color: 'white'}"
    >
      <div class="app">
        <!-- <img :src="msg.user.headerUrl" style="width:50px;height:50px" />
        <p>{{msg.msg}}</p> -->
      </div>
    </van-popup>
  </div>
</template>

<script>
//新消息弹出框组件
export default {
  name: "message",
  components: {},
  props: ["isShow", "msg"],
  data() {
    return {};
  },
  created() {
    // console.log(msg);
  }
};
</script>

<style>
.app {
  text-align: center;
  line-height: 40px;
  background: rgba(0, 0, 0, 0.35);
  /* color: white; */
}
</style>



