<template>
  <div id="app">
    <welcome></welcome>
    <div class="outter" :class="{'hideLeft':$route.path.split('/').length>2}">
      <!--通用头部-->
      <header class="app-header" :class="{'header-hide':!$store.state.headerStatus}">
        <wx-header :pageName="pageName"></wx-header>
      </header>
      <!--搜索框 只在“微信”和“通讯录”页面下显示-->
      <search
        v-show="$route.path.indexOf('explore')===-1&&$route.path.indexOf('self')===-1&&$route.path.indexOf('login')===-1&&$route.path.indexOf('workbench')===-1&&$route.path.indexOf('warehousing')===-1&&$route.path.indexOf('app')===-1"
      ></search>
      <!--四个门面页 “微信” “通讯录” “发现” “我”-->
      <section class="app-content">
        <!--<keep-alive exclude="/warehousing">-->
        <router-view name="default"></router-view>
        <!--</keep-alive>-->
      </section>
      <!--底部导航 路由 -->
      <footer
        class="app-footer"
        v-show="$route.path.indexOf('login')===-1&&$route.path.indexOf('warehousing')===-1"
      >
        <wx-nav></wx-nav>
      </footer>
    </div>
    <!--其他店内页集合 有过渡效果-->
    <transition
      name="custom-classes-transition"
      :enter-active-class="enterAnimate"
      :leave-active-class="leaveAnimate"
    >
      <router-view name="subPage" class="sub-page"></router-view>
    </transition>
    <van-popup v-model="show" position="top" :overlay="false" :style="{ height: '50px' }">
      <!-- <div class="boxNews">{{boxNews.user.nickname}}:{{boxNews.msg}}</div> -->
    </van-popup>
    <!-- <message :isShow="true" :msg="data"></message> -->
  </div>
</template>

<script>
import welcome from "./components/common/welcome.vue";
import WxHeader from "./components/common/wx-header";
import WxNav from "./components/common/wx-nav";
import search from "./components/common/search";
import { sendSock } from "./socket/socket";
import { Dialog, Toast, Notify, Popup } from "vant";
import message from "./components/common/message";
import mixin from "./vuex/mixin.js"; // 混合被单独放在 mixin.js 中管理
window.mixin = mixin; // 将 混合/mixin 暴露在窗口对象中，某些组件需要时，直接提取 window.mixin
export default {
  name: "app",
  components: {
    WxHeader,
    WxNav,
    search,
    welcome,
    message
  },
  data() {
    return {
      boxNews: "",
      show: false,
      pageName: "",
      routerAnimate: false,
      enterAnimate: "", //页面进入动效
      leaveAnimate: "" //页面离开动效
    };
  },
  created() {
    this.getSysExp(); //表情加载
    let loginFlag = window.localStorage.getItem("loginFlag");
    if (loginFlag !== "") {
      let userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
      let userId = userInfo.userId;
      sendSock(userId);
    }
  },
  computed: {
    toastMsg() {
      return this.$store.state.toastMsg;
    }
  },
  methods: {
    //表情加载
    getSysExp() {
      this.$http.get("/api/set/sys/getSysExp").then(res => {
        if (res.data.code === 200) {
          //保存表情到store
          this.$store.commit("getSysExp", res.data.data);
        }
      });
    }
  },
  watch: {
    // 监听 $route 为店内页设置不同的过渡效果
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      if (toDepth === 2) {
        this.$store.commit("setPageName", to.name);
      }
      //同一级页面无需设置过渡效果
      if (toDepth === fromDepth) {
        return;
      }
      this.enterAnimate =
        toDepth > fromDepth ? "animated fadeInRight" : "animated fadeInLeft";
      this.leaveAnimate =
        toDepth > fromDepth ? "animated fadeOutLeft" : "animated fadeOutRight";
      // 从店面页进入店内页 需要对店内页重新设置离开动效 因为他们处于不同 name 的 router-view
      if (toDepth === 3) {
        this.leaveAnimate = "animated fadeOutRight";
      }
    },
    //监听消息弹框
    toastMsg(newValue, oldValue) {
      console.log(newValue);

      // if (newValue.length != 0) {
      //   this.boxNews = newValue;
      //   this.show = true;
      //   setTimeout(() => {
      //     this.show = false;
      //   }, 3000);
      // }
    }
  }
};
</script>
<style>
/*将公用的样式统一在此导入*/

@import "assets/css/base.css";
@import "assets/css/common.css";
@import "assets/css/wx-header.css";
/*阿里 fonticon*/

@import "assets/css/lib/iconfont.css";
/*过渡效果需要的动画库*/

@import "assets/css/lib/animate.css";
/*weui 样式库 非常适合高仿微信*/

@import "assets/css/lib/weui.min.css";
#wx-header {
  background: #f7f6f6;
  color: black;
}
.boxNews {
  line-height: 50px;
  text-align: center;
}
</style>



